<template>
  <div>
    <mu-appbar style="width: 100%;" color="primary">
      <mu-button icon slot="left" @click="closeLeftBar">
        <mu-icon value="menu" ></mu-icon>
      </mu-button>
      财院密码修改工具
      <mu-menu slot="right">
        <mu-button flat>关于</mu-button>
        <mu-list slot="content">
          <mu-list-item button @click="autherInfo">
            <mu-list-item-content>
              <mu-list-item-title>作者信息</mu-list-item-title>
            </mu-list-item-content>
          </mu-list-item>
        </mu-list>
      </mu-menu>
    </mu-appbar>
  <mu-drawer :open.sync="open" :docked="docked" :right="position === 'right'">
    <mu-list>
      <mu-list-item button @click="goHome">
        <mu-list-item-title>首页</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button @click="goPassword">
        <mu-list-item-title>密码修改</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button @click="Myconfirm">
        <mu-list-item-title>GitHub地址</mu-list-item-title>
      </mu-list-item>
    </mu-list>
  </mu-drawer>
  </div>
</template>

<script>
export default {
  name: 'LeftBar',
  data () {
    return {
      docked: false,
      open: false,
      position: 'left'
    }
  },
  methods: {
    closeLeftBar () {
      this.open = !this.open
    },
    goPassword () {
      this.$router.push({ path: '/pwd' })
      this.closeLeftBar()
    },
    goHome () {
      this.$router.push({ path: '/' })
      this.closeLeftBar()
    },
    Myconfirm () {
      this.$confirm('点击确定跳转至Github页面', '提示').then(({ result }) => {
        if (result) {
          window.location = 'https://github.com/kimmosc2/hedu-basic'
        } else {
          // self.$toast.message('点击了取消')
        }
      })
    },
    autherInfo () {
      this.$alert('河南财政金融学院17级软件技术-张x', '作者信息', {
        okLabel: '厉害了'
      })
    }
  }
}
</script>

